// 数据分析
export function dataAnalysis(data, filter) {
  data = toLogStr(data)

  const logs = data.split('\n')
  const sortLogs = []
  const serverLogs = []
  const uniqueLogs = {}
  const uniqueLogsHit = {}
  const uniqueApps = {}
  const uniqueTrackIds = {}
  const sortLogsMap = {}
  const uniqueGroups = {}
  const errorLogs = []
  for (let i = 0; i < logs.length; i++) {
    const temp = logs[i]
    try {
      const log = JSON.parse(temp.replaceAll(/^[^{]*?:({.*)/g, '$1'))
      if (log.type === 'PROTOCAL' && log.protocal.role === 'CLIENT') {
        log.protocal.role = 'SENDER'
      }
      if (log.type === 'PROTOCAL' && log.protocal.role === 'SERVER') {
        log.protocal.role = 'RECEIVER'
      }
      if (log.trackId === '') {
        log.trackId = '未填写'
      }
      // 跳过已经过滤掉的模块日志
      if (filter && Object.keys(filter.groups).length !== 0 && !filter.applications[log.application.name]) {
        continue
      }
      if (log.type === 'DEFAULT') {
        sortLogs.push(log)
        if (sortLogsMap[log.timestamp]) {
          sortLogsMap[log.timestamp].push(log)
        } else {
          sortLogsMap[log.timestamp] = [log]
        }
      } else if (log.type === 'PROTOCAL' && log.protocal.role === 'SENDER') {
        sortLogs.push(log)
        if (log.protocal.request.id) {
          uniqueLogs[log.protocal.request.id] = log.protocal.request.id
        }
        if (sortLogsMap[log.timestamp]) {
          sortLogsMap[log.timestamp].push(log)
        } else {
          sortLogsMap[log.timestamp] = [log]
        }
      } else if (log.type === 'PROTOCAL' && log.protocal.role === 'RECEIVER') {
        serverLogs.push(log)
        if (sortLogsMap[log.timestamp]) {
          sortLogsMap[log.timestamp].push(log)
        } else {
          sortLogsMap[log.timestamp] = [log]
        }
      }
      if (log.trackId) {
        uniqueTrackIds[log.trackId] = log.trackId
      }
      if (log.application.name) {
        uniqueApps[log.application.name] = log.application.name
      }

      if (log.protocal) {
        if (!log.protocal.source.group || !log.protocal.destination.group) {
          console.log(log)
        }
        uniqueGroups[log.protocal.source.group] = log.protocal.source.group
        uniqueGroups[log.protocal.destination.group] = log.protocal.destination.group
      }
    } catch (error) {
      console.log('非json:' + temp)
      errorLogs.push({'line': i, 'log':temp})
    }
  }
  for (let i = 0; i < serverLogs.length; i++) {
    const log = serverLogs[i]
    if (log.protocal.request.id) {
      if (uniqueLogs[log.protocal.request.id]) {
        uniqueLogsHit[log.protocal.request.id] = log.protocal.request.id
      } else {
        sortLogs.push(log)
      }
    } else {
      sortLogs.push(log)
    }
  }
  sortLogs.sort(function(a, b) {
    let time1 = new Date(a.timestamp).getTime()
    let time2 = new Date(b.timestamp).getTime()
    if (a.timestamp.length === 31) {
      time1 = time1 * 1000 + Number(a.timestamp.substring(23, 26))
      time2 = time2 * 1000 + Number(b.timestamp.substring(23, 26))
    }
    return time1 - time2
  })
  return {
    'sortLogs': sortLogs,
    'serverLogs': serverLogs,
    'uniqueLogs': uniqueLogs,
    'uniqueLogsHit': uniqueLogsHit,
    'uniqueTrackIds': uniqueTrackIds,
    'uniqueApps': uniqueApps,
    'sortLogsMap': sortLogsMap,
    'uniqueGroups': uniqueGroups,
    'errorLogs': errorLogs
  }
}

// 图结构化数据生成
export function diagramDataGeneration(data, filter) {
  const mermaidBuffer = []
  mermaidBuffer.push('sequenceDiagram')
  mermaidBuffer.push('autonumber')
  // mermaidBuffer.push("%%{init: { 'sequence': {'messageAlign': 'left','noteAlign': 'left'} }}%%")

  for (let i = filter.logRange[0]; i < filter.logRange[1]; i++) {
    const log = data.sortLogs[i]
    // 全文过滤
    if (filter && filter.contextReg && filter.contextReg.exec(JSON.stringify(log)) == null) {
      continue
    }

    if (filter && Object.keys(filter.trackIds).length !== 0 && !filter.trackIds[log.trackId]) {
      continue
    }

    if (filter && Object.keys(filter.applications).length !== 0 && !filter.applications[log.application.name]) {
      continue
    }

    if (log.protocal && log.protocal.message) {
      log.protocal.message = log.protocal.message.split('\n')[0]
    }
    if (log.message) {
      log.message = log.message.split('\n')[0]
    }
    if (log.protocal && (!log.protocal.source.group || !log.protocal.destination.group)) {
      console.log('非法日志:' + JSON.stringify(log))
      continue
    }

    if (log.type === 'DEFAULT' && filter && filter.logTypeDefault) {
      mermaidBuffer.push('Note over [' + log.application.name + ']:(' + i + ')' + log.timestamp + '<br/>' + (log.message.length > 100 ? log.message.substring(0, 100) + '...' : log.message))
    } else if (log.type === 'PROTOCAL' && filter && filter.logTypeProtocal) {
      if (filter && Object.keys(filter.groups).length !== 0 && (!filter.groups[log.protocal.source.group] || !filter.groups[log.protocal.destination.group])) {
        continue
      }

      if (filter && filter.uriIncludeReg && filter.uriIncludeReg.exec(log.protocal.request.uri) == null) {
        continue
      }

      if (filter && filter.uriExcludeReg && filter.uriExcludeReg.exec(log.protocal.request.uri) != null) {
        continue
      }

      let role = log.protocal.role
      if (log.protocal.role === 'SENDER' && data.uniqueLogsHit[log.protocal.request.id]) {
        role = 'RECEIVER,SENDER'
      }
      role = role + '|' + i

      let prefix = log.protocal.type
      if (log.protocal.duration) {
        if (log.protocal.request.timestamp.length === 31) {
          prefix = prefix + ',' + log.protocal.duration / 1000 + 'ms'
        } else {
          prefix = prefix + ',' + log.protocal.duration + 'ms'
        }
      }

      if (log.protocal.code === 'SUCCESS') {
        if (log.protocal.request.method) {
          mermaidBuffer.push('[' + log.protocal.source.group.replaceAll('+', '#43;').replaceAll(':', '#58;') + ']' + ' ->> ' + '[' + log.protocal.destination.group.replaceAll('+', '#43;').replaceAll(':', '#58;') + ']' + ':(' + role + ')' + log.timestamp + '<br/>[' + prefix + '](' + log.protocal.request.method + ')' + log.protocal.request.uri)
        } else {
          mermaidBuffer.push('[' + log.protocal.source.group.replaceAll('+', '#43;').replaceAll(':', '#58;') + ']' + ' ->> ' + '[' + log.protocal.destination.group.replaceAll('+', '#43;').replaceAll(':', '#58;') + ']' + ':(' + role + ')' + log.timestamp + '<br/>[' + prefix + ']' + log.protocal.request.uri)
        }
      } else {
        mermaidBuffer.push('rect rgb(193, 44, 31)')
        if (log.protocal.request.method) {
          mermaidBuffer.push('[' + log.protocal.source.group.replaceAll('+', '#43;').replaceAll(':', '#58;') + ']' + ' -x ' + '[' + log.protocal.destination.group.replaceAll('+', '#43;').replaceAll(':', '#58;') + ']' + ':(' + role + ')' + log.timestamp + '<br/>[' + prefix + '](' + log.protocal.request.method + ')' + log.protocal.request.uri + '<br/>' + log.protocal.code + ': ' + log.protocal.message)
        } else {
          mermaidBuffer.push('[' + log.protocal.source.group.replaceAll('+', '#43;').replaceAll(':', '#58;') + ']' + ' -x ' + '[' + log.protocal.destination.group.replaceAll('+', '#43;').replaceAll(':', '#58;') + ']' + ':(' + role + ')' + log.timestamp + '<br/>[' + prefix + ']' + log.protocal.request.uri + '<br/>' + log.protocal.code + ': ' + log.protocal.message)
        }
        mermaidBuffer.push('end')
      }
    }
  }
  // console.log("最终输出时序图: \n" + mermaidBuffer.join("\n"))
  if (mermaidBuffer.length < 3) {
    return
  }
  return mermaidBuffer.join('\n')
}
